<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

      </van-pull-refresh>
    </section>
  </div>
</template>

<script>
  export default {
    name: "linked",
    data(){
      return {
        title: '业务人员联系方式',
        isLoading: false,
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

</style>



